import { Movie, MovieService } from './movie.service';
import { Component, OnInit } from '@angular/core';


@Component({
  selector: 'movie',
  template: `
  	<md-grid-list cols="3" rowHeight="3:4" >
	  <md-grid-tile *ngFor="let movie of movies" (click)="onSelect(movie)">
	  	<md-card>
		  <img [src]="movie.poster_link" md-card-image layout-fill/>
		</md-card>
		<md-grid-tile-footer>		
			<h3 md-line>{{movie.cht_title}}</h3>
			<span md-line>&nbsp;</span>
            <span md-line>IMDB: {{movie.imdb.rating}}</span>
			<span md-line>爛番茄: {{movie.tomatoes.meter}}</span>
			<md-icon>star_border</md-icon>
		</md-grid-tile-footer>
	  </md-grid-tile>
	</md-grid-list>
  `,
})
export class MovieComponent implements OnInit {
	private ms:MovieService
	private movies:Movie[]
	constructor(private _movieService:MovieService) {
		this.ms = _movieService
	}

	ngOnInit() {
		//this.movies = this.ms.getMovies()
		this.ms.getMovies().subscribe(movies=>{
			this.movies = movies;
		})
  	}

	onSelect(movie:Movie) {
		console.log(movie)
	}
}


